import React from 'react';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import PostContainer from "./MyPosts/PostContainer";

const Profile = (props) => {

    return (
        <div >
            <ProfileInfo profile={props.profile}/>
            <PostContainer
            />
        </div>
    )
}

export default Profile;
